@import "../colors";
@import url("https://fonts.googleapis.com/css2?family=Lato:wght@100;400;700&family=Roboto+Mono:wght@100;400&display=swap");

.encabezadoCursos{
    margin: 50px 0 0; 
    padding: 20px 0 20px 60px;
    background: rgb(20, 23, 36);
    border-top: solid 1px gray;
    border-bottom: solid 1px gray;
}

.contenidoCurso {
    margin: 40px;
    width: 90%;
    padding: 30px;
    font-size: 1em;
    border-radius: 20px;
    background: rgb(20, 23, 36);
    
    a {
        text-decoration: none;
        display: flex;
        color: $textColor; 
        justify-content: center;
        i {
            display: flex;
            font-size: 1em;
            padding-top: 5px;
            padding-right: 20px;
        }
        p {
           color: $textColor;
           
        }
    }
}

.wrapperClasses{
    display: grid;
    grid-template-columns:  500px 200px 200px 150px 100px ;
    gap: 25px;
    margin-bottom: 20px;
    align-items: center;
}
.tituloClase{
    grid-column: 1 / 5;
    grid-row: 1;
    width: 500px;
    font-size: 0.8em;
}

.grabacionClaseLunes{
    border: 1px solid gray;
    border-radius: 12px;
    padding: 8px;
    width: 200px;
    grid-column: 2 / 5;
    grid-row: 1;
    
}

.grabacionClaseMartes{
    border: 1px solid gray;
    border-radius: 12px;
    padding: 8px;
    width: 200px;
    grid-column: 3 / 5;
    grid-row: 1;
    
}

.materialClase{
    padding: 8px;
    width: 150px;
    border: 1px solid gray;
    border-radius: 12px;
    grid-column: 4 / 5;
    grid-row: 1;
}

.pdfClase{
    grid-column: 5 / 5;
    border: 1px solid gray;
    border-radius: 12px;
    padding: 8px;
    width: 100px;
    grid-row: 1;
}