@import "../colors";
@import url('https://fonts.googleapis.com/css2?family=Lato:wght@100;400;700&family=Roboto+Mono:wght@100;400&display=swap');

footer{
    display: flex;
    width: 100vw;
    margin-top: 50px;
    margin-bottom: 20px;
    justify-content: center;
    flex-direction: column;
    bottom: 20px;
    p{
        padding-top: 10px;
        color: $textColor;
        display: flex;
        justify-content: center;
        font-family: 'Roboto Mono', monospace;
        font-size: 1.1rem;
        text-align: center;
        i{
          margin-right: 5px;
        }
    }
    }
    .socialMedia{
    display: flex;
    justify-content: center;
    margin-bottom: 15px;
    }
    
    .Btn {
    margin-right: 10px;
    margin-left: 10px;
    width: 45px;
    height: 45px;
    display: flex;
    align-items: center;
    justify-content: center;
    border: none;
    background-color: transparent;
    position: relative;
    border-radius: 7px;
    cursor: pointer;
    transition: all 0.3s;
    }
    
    .svgContainer {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: transparent;
    backdrop-filter: blur(0px);
    letter-spacing: 0.8px;
    border-radius: 10px;
    transition: all 0.3s;
    border: 1px solid rgba(156, 156, 156, 0.466);
    }
    
    .BG {
    position: absolute;
    content: "";
    width: 100%;
    height: 100%;
    background: #181818;
    z-index: -1;
    border-radius: 10px;
    pointer-events: none;
    transition: all 0.3s;
    }
    
    .Btn:hover .BG {
    transform: rotate(35deg);
    transform-origin: bottom;
    }
    
    .Btn:hover .svgContainer {
    background-color: $variantColor;
    backdrop-filter: blur(4px);
    }